export default {
	data() {
		return {
			Data:{},
			SOEquipmentList:[],
			TotalPrice:{},
			Consignee:{}
		}
	},
	created() {
		uni.request({
						url: this.$store.state.url + '/SO/GetSo',
						method: 'GET',
						data: {
							SOId: this.$store.state.SOId
						},
						header: {
							'Token': this.$store.state.SOId
						},
						success: (res) => {
							this.Data = JSON.parse(res.data).Data
							console.log(this.Data)
							this.SOEquipmentList = JSON.parse(res.data).Data.SOEquipmentList
							this.TotalPrice = JSON.parse(res.data).Data.TotalPrice
							this.Consignee = JSON.parse(res.data).Data.Consignee
						}
					})
	},
	methods: {  }
}
